* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f5f5f5;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.image-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.main-image {
  object-fit: cover;
  width: 500px;
  height: 1400px;
  box-shadow: 0 4px 8px #0000001a;
}

@media (width <= 768px) {
  .main-image {
    aspect-ratio: 500 / 1400;
    width: 100%;
    height: auto;
  }
}
